body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  padding: 20px 0px;
  overflow: hidden;
  background-color: #fff;
}

header .container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  overflow: hidden;
  box-sizing: border-box;
}

.header-brand {
    float: left;
}

.header-logo-img {
  margin: 0px 20px 0px 0px;
  height: 40px;
  vertical-align: middle;
}

.header-slogan {
  color: #999;
  display: inline-block;
  font-size: 12px;
  margin: 0px;
  vertical-align: 2px;
}

.header-slogan-slash {
  color: rgba(0,0,0,0.1);
  font-size: 46px;
  font-weight: 300;
  margin: 0px 10px 0px 0px;
  vertical-align: middle;
}
.header-slogan-text {
  vertical-align: middle;
  font-weight: 600;
}



@media (min-width: 1200px) {
  header .container {
      width: 992px;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  header .container {
    width: 970px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  header .container {
    width: 750px;
  }
}

@media (max-width: 768px) {
  header{
    height: 103px;
    box-sizing: border-box;
  }
  .header-slogan {
    display: none;
  }
  .header-brand {
    float: none;
    float: initial;
  }
}

 .container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  overflow: hidden;
  box-sizing: border-box;
}


@media (min-width: 1200px) {
  .container {
      width: 992px;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .container {
    width: 750px;
  }
}


footer .footer-top {
  background-color: #fcfcfc;
  border-top: 1px solid #e9e9e9;
  color: #777;
  padding: 70px 0px;
  box-sizing: border-box;
}

.footer-top-left-content {
  float: left;
  width: calc(100% / 12 * 8);
}

.footer-top-left-content img,
.footer-top-left-content p {
  display: inline-block;
}

.footer-top-left-content p {
  text-align: left;
  margin: 5px 0 25px 0;
  font-size: 13px;
  line-height: 24px;
}

.footer-top-left-content img {
  float: left;
  padding-bottom: 25px;
}

.footer-top-right-content {
  float: left;
  width: calc((100% / 12 * 4) - 82.156px);
  margin-left: 82.156px;
  padding: 0 15px;
  box-sizing: border-box;
  text-align: left;
}

.footer-top-right-content-information {
  margin: 0px 0px 30px 0px;
  padding: 0px;
  color: #363636;
  font-size: 20px;
  font-weight: 300;
  line-height: 38px;
}

.footer-top-right-content-contact {
  display: block;
  padding: 8px 0px;
  color: #777;
  font-size: 13px;
  text-decoration: none;
}

.footer-top-right-content-contact:hover {
  text-decoration: underline;
}

footer .footer-bottom {
  background-color: #f7f7f7;
  color: #777;
  font-size: 12px;
  padding: 40px 0px 38px;
  box-sizing: border-box;
}

.footer-bottom-right-content-text {
  text-align: left;
  margin: 0 0 1px 0;
  overflow: -webkit-paged-y;
}

.footer-bottom-right-content-text a {
  color: #777;
  text-decoration: none;
  border-bottom: 1px solid #777;
}

@media (max-width: 768px) {
  .footer-top {
    min-height: 446px;
  }
  .footer-bottom {
    min-height: 112px;
  }
  .footer-top-left-content,
  .footer-top-right-content {
    width: 100%;
    margin-left: 0;
    padding: 0;
  }
  .footer-top-left-content img {
    padding-top: 30px;
  }
  .footer-top-right-content-information {
    margin-top: 30px;
  }
}
body {
  background-color: #f7f7f7;
  font-family: 'Open Sans', sans-serif;
  line-height: 1.42857;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

